import { axios } from '@/utils/request'

const api = {
    accountsList: '/accounts',
    trackRecordOfDetail: '/accounttrack/accountlink/',
    getDetailCustomer:'/accounts/',
    // get my info
    userInfo: '/user/info'
}
export default api

/**
 * login func
 * parameter: {
 *     username: '',
 *     password: '',
 *     remember_me: true,
 * }
 * @param parameter
 * @returns {*}
 */
export function getAccountsInfoList() {
    return axios({
        url: api.accountsList,
        method: 'get',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    })
}
export function getTrackRecordOfDetail(id) {
    return axios({
        url: api.trackRecordOfDetail + id,
        method:'get',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    })
}
export function getTrackRecordOfDetailByParams (id, parameter) {
    return axios({
        url: api.trackRecordOfDetail + id,
        method:'get',
        params: parameter,
    })
}

export function getDetailCustomer(id){
    return axios({
        url: api.getDetailCustomer+id,
        method:'get',
        headers: {
            'Content-Type': 'application/json;charset=UTF-8'
        }
    })
}

export function login(parameter) {
    return axios({
        url: api.login,
        method: 'post',
        data: parameter
    })
}


