import { axios } from '@/utils/request'

const api = {
    getTodayStar: '/todaystar',
    getActivity: '/activity',
    getTeams: '/teams'
}
export default api

export function getTodayStar() {
    return axios({
        url: api.getTodayStar,
        method: 'get',
    })
}

export function getActivity() {
    return axios({
        url: api.getActivity,
        method: 'get',
    })
}

export function getTeams() {
    return axios({
        url: api.getTeams,
        method: 'get',
    })
}



