<template>
  <div class="query">
    <div class="interval-sovle">
      <a-button
        class="query-add-btn"
        style="margin-bottom:8px;margin-right:8px"
        @click="handleAdd"
      >增加条件</a-button>
      <a-button
        class="query-delete-btn"
        style="margin-bottom:8px"
        @click="onDeleteAll"
      >删除全部条件</a-button>
    </div>
    <a-form :form="form">
      <a-table
        bordered
        :dataSource="dataSource"
        :columns="columns"
      >
        <template
          slot="fields"
          slot-scope="text, record"
        >
          <a-select
            :key="record.key"
            :defaultValue="record.fieldSelected"
            @change="handleFieldChange(record.key)"
            @select="getIdFromSelected"
          >
            <a-select-option
              disabled
              value=""
            >请选择</a-select-option>
            <a-select-option
              v-for="item in structure.fields"
              :key="item.id"
              v-bind:value="item.field"
            >
              {{item.text}}
            </a-select-option>
          </a-select>
        </template>

        <template slot="conditions" slot-scope="text, record">
          <a-select
            defaultValue="="
            v-show="record.valueType==='input'&&!record.isNumber"
            style="width: 100px"
            @change="handleConditionChange(record.key)"
            @select="getConditionFromSelected"
          >
            <a-select-option
              v-for="item in structure.stringConditions"
              :key="item.id"
              v-bind:value="item.condition"
            >{{item.text}}
            </a-select-option>
          </a-select>
          <a-select
            defaultValue="="
            v-show="record.valueType==='input'&&record.isNumber"
            style="width: 100px"
            @change="handleConditionChange(record.key)"
            @select="getConditionFromSelected"
          >
            <a-select-option
              v-for="item in structure.numberConditions"
              :key="item.id"
              v-bind:value="item.condition"
            >{{item.text}}
            </a-select-option>
          </a-select>
          <a-select
            :disabled="true"
            defaultValue="="
            v-show="record.valueType==='date'||record.valueType==='select'||record.valueType==='datetime'"
            style="width: 100px"
            @change="handleConditionChange(record.key)"
            @select="getConditionFromSelected"
          >
            <a-select-option value="=">
              等于
            </a-select-option>
          </a-select>
        </template>

        <template slot="value" slot-scope="text, record,index">
          <a-input
            v-if="record.valueType=='input'&&record.isDuration"
            addonAfter="分"
            @change="e => handleChange(e.target.value, record.key)"
            :value="record.value"
          />
          <a-input
            v-else-if="record.valueType=='input'&&!record.isDuration"
            style="margin: -5px 0"
            @change="e => handleChange(e.target.value, record.key)"
            :value="record.value"
          />
          <!-- <range-picker
            style="margin: -5px 0"
            :ranges="{ '今日': [moment(), moment()], '本月': [moment(), moment().endOf('month')],'往后一年':[moment(),moment().add(12,'month')] }"
            @calendarChange="e =>getKey(e, record.key)"
            v-else-if="record.valueType=='datetime'"
            format="YYYY-MM-DD HH:mm:ss"
            :showTime="true"
            @change="rangeChange"
          /> -->
          <a-form-item v-else-if="record.valueType=='datetime'">
            <range-picker
              style="margin: -5px 0"
              v-decorator="[record.key.toString(),{initialValue:null}]"
              :ranges="{ '今日': [moment(), moment()], '本月': [moment(), moment().endOf('month')],'往后一年':[moment(),moment().add(12,'month')] }"
              @calendarChange="e =>getKey(e, record.key)"
              format="YYYY-MM-DD HH:mm:ss"
              :showTime="true"
              @change="rangeChange"
            />
          </a-form-item>
          <!-- <range-picker style="margin: -5px 0" :ranges="{ '今日': [moment(), moment()], '本月': [moment(), moment().endOf('month')],'往后一年':[moment(),moment().add(12,'month')] }" @calendarChange="e =>getKey(e, record.key)" v-else-if="record.valueType=='date'" format="YYYY-MM-DD"
            :showTime="false" @change="rangeChange" /> -->
          <a-form-item v-else-if="record.valueType=='date'">
            <range-picker
              style="margin: -5px 0"
              v-decorator="[record.key.toString(),{initialValue:null}]"
              :ranges="{ '今日': [moment(), moment()], '本月': [moment(), moment().endOf('month')],'往后一年':[moment(),moment().add(12,'month')] }"
              @calendarChange="e =>getKey(e, record.key)"
              format="YYYY-MM-DD"
              :showTime="false"
              @change="rangeChange"
            />
          </a-form-item>
          <a-select
            v-else-if="record.valueType=='select'"
            :value="record.value"
            defaultValue=""
            @change="handleSelectChange(record.key)"
            @select="getValueFromSelected"
          >
            <a-select-option
              disabled
              value=""
            >请选择</a-select-option>
            <a-select-option
              v-for="(value,key) in record.options"
              :key="key"
              :value="key"
            >{{value}}</a-select-option>
          </a-select>
        </template>

        <template slot="connectionWay" slot-scope="text, record">
          <a-select
            class="connectionWay"
            defaultValue="and"
            @change="handleConnectionChange(record.key)"
          >
            <a-select-option value="and">与</a-select-option>
            <a-select-option value="or">或</a-select-option>
          </a-select>
        </template>

        <template slot="operation" slot-scope="text, record">
          <a-popconfirm
            v-if="dataSource.length"
            title="您确认要删除吗？"
            @confirm="() => onDelete(record.key)"
          >
            <a href="javascript:;">删除</a>
          </a-popconfirm>
        </template>
      </a-table>
    </a-form>
  </div>
</template>

<script>
import Vue from "vue";
import moment from "moment";
import AButton from "ant-design-vue/lib/button";
import ATable from "ant-design-vue/lib/table";
import ASelect from "ant-design-vue/lib/select";
import AInput from "ant-design-vue/lib/input";
import APopconfirm from "ant-design-vue/lib/popconfirm";
import DatePicker from "ant-design-vue/lib/date-picker";
import AForm from "ant-design-vue/lib/form";
// import {
//   Button,
//   Table,
//   Select,
//   Input,
//   Popconfirm,
//   message,
//   DatePicker
// } from "ant-design-vue";

// import { constants } from 'http2';
/*
 * EditableCell Code https://github.com/vueComponent/ant-design-vue/blob/master/components/table/demo/EditableCell.vue
 */
const ASelectOption = ASelect.Option;
const { RangePicker } = DatePicker;
const AFormItem = AForm.Item;
export default {
  props: {
    structure: Object
  },
  components: {
    AButton,
    ATable,
    ASelect,
    AInput,
    ASelectOption,
    APopconfirm,
    DatePicker,
    RangePicker,
    AForm,
    AFormItem
  },
  data() {
    return {
      searchConditionResult: [],
      confirmLoading: false,
      currentValue: "", //当前被选中的field值
      currentKey: "",
      currentCondition: "",
      currentValueFromSelect: "",
      currentSelectedType: "",
      currentSelectedIsDuration: false,
      currentTimeInterval: {}, //当前时间区间
      start: "",
      end: "",
      dataSource: [
        {
          key: 0,
          fieldSelected: "",
          conditionSelected: "=",
          valueType: "input",
          isShowTime: false,
          isDuration: false,
          isNumber: false,
          options: [],
          value: "",
          link: "and"
        }
      ],
      count: 1,
      columns: [
        {
          title: "字段",
          dataIndex: "fields",
          scopedSlots: {
            customRender: "fields"
          }
        },
        {
          title: "条件",
          dataIndex: "conditions",
          scopedSlots: {
            customRender: "conditions"
          }
        },
        {
          title: "值",
          dataIndex: "value",
          scopedSlots: {
            customRender: "value"
          }
        },
        {
          title: "联接",
          dataIndex: "connectionWay",
          scopedSlots: {
            customRender: "connectionWay"
          }
        },
        {
          title: "操作",
          dataIndex: "operation",
          scopedSlots: {
            customRender: "operation"
          }
        }
      ]
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  created() {},
  methods: {
    moment,
    handleChange(value, key) {
      if (this.dataSource.filter(item => key === item.key)[0]) {
        this.dataSource.filter(item => key === item.key)[0].value = value;
      } else {
        return;
      }
    },
    getValueFromSelected(value) {
      this.currentValueFromSelect = value;
    },
    handleSelectChange(key) {
      if (this.dataSource.filter(item => key === item.key)[0]) {
        this.dataSource.filter(
          item => key === item.key
        )[0].value = this.currentValueFromSelect;
      }
    },
    onDelete(key) {
      const dataSource = [...this.dataSource];
      this.dataSource = dataSource.filter(item => item.key !== key);
    },
    onDeleteAll() {
      const dataSource = [...this.dataSource];
      this.dataSource = dataSource.filter(item => null);
    },
    handleAdd() {
      const { count, dataSource } = this;
      const newData = {
        key: count,
        fieldSelected: "",
        conditionSelected: "=",
        valueType: "input",
        isDuration: false,
        isShowTime: false,
        isNumber: false,
        options: [],
        value: "",
        link: "and"
      };
      this.dataSource = [...dataSource, newData];
      this.count = count + 1;
    },
    handleFieldChange(key) {
      if (this.dataSource.filter(item => key === item.key)[0]) {
        //          删除已经存在的key及其对应信息
        // this.dataSource = this.dataSource.filter(
        //   item => key != item.key
        // );
        //          将最新的搜索条件添加到被筛选的搜索结果中
        // this.dataSource.filter(
        //   item => key === item.key
        // )[0].fieldSelected=this.currentValue
        this.form.resetFields(key.toString())
        const newData = {
          key: key,
          fieldSelected: this.currentValue,
          conditionSelected: "=",
          valueType: this.currentSelectedType,
          isDuration: false,
          isShowTime: false,
          isNumber: false,
          options: [],
          // value:
          //   (this.dataSource.filter(item => key === item.key)[0].valueType ===
          //     "date") |
          //     (this.dataSource.filter(item => key === item.key)[0].valueType ===
          //       "datetime") &&
          //   (this.currentSelectedType === "date" ||
          //     this.currentSelectedType === "datetime")
          //     ? this.dataSource.filter(item => key === item.key)[0].value ||
          //       null
          //     : "",
          value:"",
          link: "and"
        };
        this.currentValueFromSelect = ""; //字段改变成另一个select后，select的value没有变化，在此手动清空
        //this.dataSource.indexOf(this.dataSource.filter(item => key === item.key)[0])
        //以上代码为找到该key的元素所对应的下标，key并不是原有信息的下标
        this.dataSource.splice(
          this.dataSource.indexOf(
            this.dataSource.filter(item => key === item.key)[0]
          ),
          1,
          newData
        );
      } else {
        // this.dataSource.filter(
        //   item => key === item.key
        // )[0].fieldSelected=this.currentValue
      }
      this.judgeType(key);
      //         // if(value=="注册日期"||value=="保险到期"||value=="预约时间"){
      //         //   this.data[record.key].valuePickedField.type='date'
      //         // }else{
      //         //   this.data[record.key].valuePickedField.type='input'
      //         // }
    },
    judgeType(key) {
      //根据字段类型，实时更新值为input、TimePickr
      if (
        this.currentSelectedType == "input" &&
        !this.$props.structure.fields.filter(
          item => item.field == this.currentValue
        )[0].isNumber
      ) {
        this.dataSource.filter(item => item.key == key)[0].valueType = "input";
        this.dataSource.filter(
          item => item.key == key
        )[0].isDuration = this.currentSelectedIsDuration;
        this.dataSource.filter(item => item.key == key)[0].isNumber = false;
      } else if (
        this.currentSelectedType == "input" &&
        this.$props.structure.fields.filter(
          item => item.field == this.currentValue
        )[0].isNumber
      ) {
        this.dataSource.filter(item => item.key == key)[0].valueType = "input";
        this.dataSource.filter(
          item => item.key == key
        )[0].isDuration = this.currentSelectedIsDuration;
        this.dataSource.filter(item => item.key == key)[0].isNumber = true;
      } else if (this.currentSelectedType == "date") {
        // this.dataSource[key].valueType="date"
        this.dataSource.filter(item => item.key == key)[0].valueType = "date";
      } else if (this.currentSelectedType == "datetime") {
        this.dataSource.filter(item => item.key == key)[0].valueType =
          "datetime";
      } else if (this.currentSelectedType == "select") {
        this.dataSource.filter(item => item.key == key)[0].valueType = "select";
        this.dataSource.filter(
          item => item.key == key
        )[0].options = this.$props.structure.fields.filter(
          item => item.field == this.currentValue
        )[0].options;
      } else {
        this.$notification['warning']({
          message: this.currentSelectedType,
        })
      }
    },
    getIdFromSelected(value) {
      this.currentValue = value;
      this.currentSelectedType = this.$props.structure.fields.filter(
        item => item.field == value
      )[0].type;
      this.currentSelectedIsDuration = this.$props.structure.fields.filter(
        item => item.field == value
      )[0].isDuration;
    },
    getConditionFromSelected(value) {
      this.currentCondition = value;
    },
    handleConditionChange(key) {
      if (this.dataSource.filter(item => key === item.key)[0]) {
        this.dataSource.filter(
          item => key === item.key
        )[0].conditionSelected = this.currentCondition;
      }
    },
    handleConnectionChange(key) {
      if (this.dataSource.filter(item => key === item.key)[0]) {
        this.dataSource.filter(item => key === item.key)[0].link = this.link;
      }
    },
    //将官方提供的事件方法改为获取当前操作的选择器的key
    getKey(e, key) {
      this.currentKey = key;
    },
    rangeChange(value, dataString) {
      let key = this.currentKey;
      let valueType = this.dataSource.filter(item => item.key == key)[0]
        .valueType;
      // this.start=this.moment(value[0]).format(valueType==='datetime'?'YYYY-MM-DD HH:mm:ss':'YYYY-MM-DD')
      // this.end=this.moment(value[1]).format(valueType==='datetime'?'YYYY-MM-DD HH:mm:ss':'YYYY-MM-DD')
      this.start = dataString[0];
      this.end = dataString[1];
      this.dataSource.filter(item => item.key == key)[0].value =
        this.start + "~" + this.end;
    }
  }
};
</script>

<style>
.ant-table-tbody > tr:last-child > td > div.connectionWay {
  display: none;
}

.timePicker input {
  width: 200px;
}

table tr > th[key="fields"] {
  width: 170px;
}

table tr > th[key="conditions"] {
  width: 133px;
}

table tr > th[key="connectionWay"] {
  width: 91px;
}

table tr > th[key="operation"] {
  width: 60px;
}

.query .ant-calendar-picker-input {
  width: 370px;
}

.query div.ant-select {
  width: 100%;
}
.query .ant-form-item{
  margin-bottom: 0
}
.query .ant-form-item .ant-form-item-control-wrapper{
  text-align: left
}
</style>




