<template>
  <page-layout :title="title">
    <div class="addCustomerInfo transparent-background">
      <customer-info-handle></customer-info-handle>
    </div>
  </page-layout>
</template>
<script>
import CustomerInfoHandle from "./CustomerInfoHandle.vue";
import PageLayout from '@/components/Page/PageLayout'
export default {
  components: {
    CustomerInfoHandle,
    PageLayout
  },
  data(){
    return{
      customersInfo:[]
    }
  },
  computed:{
    title () {
        return this.$route.meta.title
    },
  }
};
</script>





