<script>
import AForm from "ant-design-vue/lib/form";
import ACard from "ant-design-vue/lib/card";
import ARow from "ant-design-vue/lib/row";
import ACol from "ant-design-vue/lib/col";
import ASelect from "ant-design-vue/lib/select";
import AInput from "ant-design-vue/lib/input";
import AButton from "ant-design-vue/lib/button";
import ADatePicker from "ant-design-vue/lib/date-picker";
import ATooltip from "ant-design-vue/lib/tooltip";
import ASpin from 'ant-design-vue/lib/spin'
import FooterToolBar from '@/components/FooterToolbar'
import { mixin, mixinDevice } from "@/utils/mixin.js";
const AFormItem = AForm.Item;
const ASelectOption = ASelect.Option;
const ATextarea = AInput.TextArea;
export default {
  props: {
    customerInfoData: Object,
    addLoading:{
      type: Boolean,
      default: true
    },
    spinning:{
      type: Boolean,
      default: false
    }
  },
  components: {
    AForm,
    AFormItem,
    ACard,
    ARow,
    ACol,
    ASelect,
    ASelectOption,
    AInput,
    ATextarea,
    AButton,
    ADatePicker,
    ATooltip,
    ASpin,
    FooterToolBar
  },
  mixins: [mixin, mixinDevice],
  data() {
    return {
      accounts_dictionary:
        this.$localstorage.getLocalStorage("accounts_dictionary") || null
    };
  },
  beforeCreate() {
    this.form = this.$form.createForm(this);
  },
  computed: {
    custInfoData() {
      return this.$props.customerInfoData;
    }
  },
  methods: {
    handleSubmit() {
      // e.preventDefault();
      // this.$props.submitLoading = true
      this.form.validateFields((err, values) => {
        if (!err) {
          for (let item in values) {
            if (typeof values[item] !== "string") {
              if (item !== "register_date") {
                values[item] = values[item]
                  ? values[item].format("YYYY-MM-DD HH:mm:ss")
                  : "";
              } else {
                values[item] = values[item]
                  ? values[item].format("YYYY-MM-DD")
                  : "";
              }
            }
          }
          _this.$emit("addCustomerInfo", values);
        }
      });
    },
  }
};
</script>

