<template>
  <div class="customerInfoHandle">
    <a-spin
      :spinning="spinning"
      size="default"
    >
      <detail-info
        ref="dialogtreeInfo"
        style="min-height:50px"
        v-if="isDetail"
        :spinning="spinning"
        :customerInfoData="customerInfoData"
      />
      <add-info
        ref="dialogtreeInfo"
        style="min-height:50px"
        v-if="!isDetail"
        :spinning="spinning"
        :customerInfoData="customerInfoData"
        :addLoading = "addLoading"
      />
    </a-spin>
    <footer-tool-bar :style="{ width: isSideMenu() && isDesktop() ? `calc(100% - ${sidebarOpened ? 256 : 80}px)` : '100%'}">
      <div class="interval-sovle">
        <a-button v-if="!isDetail" style="margin-right: 8px;" @click="goBack">
          取消
        </a-button>
        <a-button v-else style="margin-right: 8px;" :loading="resetLoading" @click="handleReset">
          重置
        </a-button>
        <a-button type='primary' :loading="submitLoading" @click="handleSubmit">
          保存
        </a-button>
      </div>
    </footer-tool-bar>
  </div>
</template>

<script>
import AButton from 'ant-design-vue/lib/button'
import ASpin from 'ant-design-vue/lib/spin'
import FooterToolBar from '@/components/FooterToolbar'
import DetailInfo from "./DetailInfoForm.vue";
import AddInfo from "./AddInfoForm.vue"
import moment from "moment";
import { mapActions } from "vuex";
import { mixin,mixinDevice } from "@/utils/mixin.js";
import { getDetailCustomer } from '@/api/accounts.js'
export default {
  components: {
    AButton,
    ASpin,
    FooterToolBar,
    DetailInfo,
    AddInfo,
  },
  mixins: [mixin,mixinDevice],
  data() {
    return {
      spinning: false,
      customerInfoData: {},
      error_description: "",
      accounts_dictionary:
      this.$localstorage.getLocalStorage("accounts_dictionary") || null,
      isDetail:true,
      addLoading:true,
      submitLoading:false,
        resetLoading:false
    };
  },
  created() {
    let _this = this;
    if (!this.accounts_dictionary) {
      this.getDictionaryFromServer()
        .then(result => {
          if (_this.$route.path == "/customer/detail") {
            _this.isDetail = true
            _this.getCustomerDetailInfoFromServer();
          } else if (_this.$route.path == "/customer/add") {
            _this.isDetail = false
            _this.getAddCustomerInfoFromServer();
          } else if (_this.$route.path == "/customer/edit") {
            _this.getCustomerEditInfoFromServer();
          }
        })
        .catch(err => {});
    } else {
      if (this.$route.path == "/customer/detail") {
        this.isDetail = true
        this.getCustomerDetailInfoFromServer();
      } else if (this.$route.path == "/customer/add") {
        this.isDetail = false
        this.getAddCustomerInfoFromServer();
      } else if (this.$route.path == "/customer/edit") {
        this.getCustomerEditInfoFromServer();
      }
    }
  },
  mounted() {},
  computed: {
    // ...mapGetters(["currentCustInfo"])
    ID(){
      return this.currentCustInfo.id || ''
    }
  },
  watch: {
    $route(to, from) {
      let _this = this;
      if (to.path == "/customer/detail") {
        this.isDetail = true
        if (!this.accounts_dictionary) {
          this.getDictionaryFromServer()
            .then(result => {
              _this.getCustomerDetailInfoFromServer();
            })
            .catch(err => {});
        } else {
          this.getCustomerDetailInfoFromServer();
        }
      } else if (to.path == "/customer/add") {
        this.isDetail = false
        // this.addLoading = false
        if (!this.accounts_dictionary) {
          this.getDictionaryFromServer()
            .then(result => {
              _this.getAddCustomerInfoFromServer();
            })
            .catch(err => {});
        } else {
          this.getAddCustomerInfoFromServer();
        }
      } else if (to.path == "/customer/edit") {
        if (!this.accounts_dictionary) {
          this.getDictionaryFromServer()
            .then(result => {
              _this.getCustomerEditInfoFromServer();
            })
            .catch(err => {});
        } else {
          this.getCustomerEditInfoFromServer();
        }
      }
    }
  },
  methods: {
    ...mapActions(["setCurrentCustInfo"]),
    // custInfoDataNotNull(){
    //   return Object.keys(this.customerInfoData).length?true:false
    // },
    goBack() {
      window.history.go(-1);
    },
    editCustomerInfoToServer(data) {
      let _this = this;
      return new Promise((resolve, reject) => {
        _this.$api.editCustomerInfo(_this.ID, data).then((result) => {
          _this.$notification['success']({
            message: '修改成功',
          })
          _this.spinning = false
          resolve(result)
        }).catch((err) => {
          _this.spinning = false
          _this.$myNotification.sendFailedNotification(err, 6)
          reject(err)
        });
      })
    },
    handleSubmit() {
        // e.preventDefault();
        let _this = this;
        // this.$props.submitLoading = true
        this.spinning = true
        this.submitLoading = true
        this.$refs.dialogtreeInfo.form.validateFields((err, values) => {
          if (!err) {
            for (let item in values) {
              if (!values[item]) {
                delete values[item]
              } else {
                if (typeof(values[item]) !== "string") {
                  if (item !== "register_date") {
                    values[item] = values[item] ? values[item].format('YYYY-MM-DD HH:mm:ss') : ""
                  } else {
                    values[item] = values[item] ? values[item].format('YYYY-MM-DD') : ""
                  }
                }
              }

            }
            // if(_this.custInfoData.page==="create"){
            //   console.log("Received values of form: ", values);
            //   _this.$emit('addCustomerInfo',values)
            // }else if(_this.custInfoData.page==="detail"){
            if(_this.isDetail){
              _this.editCustomerInfoToServer(values).then((result) => {
                _this.submitLoading = false
              }).catch((err) => {
                _this.submitLoading = false
              });
            }else{
              _this.addCustomerInfoToServer(values).then((result) => {
                _this.submitLoading = false
              }).catch((err) => {
                _this.submitLoading = false
              });
            }
            
            // }else{
            //   _this.$message.warning("页面异常")
            // }
          }else{
            _this.spinning = false
            _this.submitLoading = false
          }
        })
      },
      // handleSelectChange(value) {
      //   console.log(value);
      //   this.form.setFieldsValue({
      //     customerName: "heiheihei"
      //   });
      // },
      // goBack(){
      //   window.history.go(-1);
      // },
      handleReset() {
        // this.$props.resetLoading = true
        let _this = this
        this.customerInfoData = {}
        this.resetLoading = true
        this.getCustomerDetailInfoFromServer().then((result) => {
          _this.resetLoading = false
        }).catch((err) => {
          _this.resetLoading = false
        });
      },
    onClose(e) {
    },
    // submitToChild() {
    //   let _this = this
    //   return new Promise((resolve,reject) =>{
    //     _this.$refs.dialogtreeInfo.handleSubmit().then((result) => {
    //       _this.spinning = false;
    //       resolve(result)
    //     }).catch((err) => {
    //       _this.spinning = false;
    //       reject(err)
    //     });
    //   })
    // },
    // resetToChild() {
    //   let _this = this
    //   return new Promise((resolve,reject)=>{
    //   _this.$refs.dialogtreeInfo.handleReset().then((result) => {
    //      _this.spinning = false;
    //      debugger
    //       resolve(result)
    //     }).catch((err) => {
    //       debugger
    //       _this.spinning = false;
    //       reject(err)
    //     });
    //   })
    // },
    getDictionaryFromServer() {
      let _this = this;
      return new Promise(function(resolve, reject) {
        const dictionaryName = "accounts";
        _this.$api
          .getDictionary(dictionaryName)
          .then(result => {
            _this.$localstorage.putLocalStorage(
              "accounts_dictionary",
              result.data
            );
            resolve(result);
          })
          .catch(err => {
            _this.$myNotification.sendFailedNotification(err)
            reject(err);
          });
      });
    },
    getAddCustomerInfoFromServer() {
      let _this = this;
      this.$api
        .getCreateCustomer()
        .then(result => {
          result.data.items.forEach(item => {
            item.data.forEach(itemChild => {
              itemChild.options = "";
              for (let key in _this.accounts_dictionary) {
                if (key === itemChild.field) {
                  itemChild.options = _this.accounts_dictionary[key];
                }
              }
              // type 为 date 日期格式需要强制转化为 moment 格式
              if (itemChild.type == "date" || itemChild.type == "datetime") {
                itemChild.value =
                  itemChild.value != "" ? moment(itemChild.value) : null;
              }
            });
          });
          _this.customerInfoData = result.data;
           _this.addLoading = false
        })
        .catch(err => {
          _this.addLoading = false
          _this.$myNotification.sendFailedNotification(err)
        });
    },
    getCustomerDetailInfoFromServer() {
      //TODO:以id为参数进行ajax请求
      //以下暂为模拟数据，具体逻辑将根据API进行后续修改
      let _this = this;
      this.spinning = true;
      this.customerInfoData = {} 

      //如果缓存被清理后，直接进入该页面，将无法从列表获取到此时的用户id，因此在此处做此处理
      if(!this.currentCustInfo){
        this.$notification['warning']({
           message: '客户信息异常，跳转至客户资料列表。',
        })
        this.$router.push('/customer/list')
        this.spinning = false;
      }else{
        return new Promise((resolve, reject) => {
          getDetailCustomer(_this.ID)
            .then(result => {
              result.items.forEach(item => {
                item.data.forEach(itemChild => {
                  itemChild.options = "";
                  for (let key in _this.accounts_dictionary) {
                    if (key === itemChild.field) {
                      itemChild.options = _this.accounts_dictionary[key];
                    }
                  }
                  // type 为 date 日期格式需要强制转化为 moment 格式
                  if (itemChild.type == "date" || itemChild.type == "datetime") {
                    itemChild.value =
                      itemChild.value != "" ? moment(itemChild.value) : null;
                  }
                  // if(itemChild.type=="timePicker"){
                  //   itemChild.value=itemChild.value?moment(itemChild.value):{}
                  // }
                });
              });
              _this.customerInfoData = result;
              _this.spinning = false;
              resolve(result);
            })
            .catch(err => {
              _this.spinning = false;
              _this.$myNotification.sendFailedNotification(err)
              reject(err);
            });
        });
      }
      
    },
    // getCustomerEditInfoFromServer() {
    //   //TODO:以id为参数进行ajax请求
    //   //以下暂为模拟数据，具体逻辑将根据API进行后续修改
    //   let _this = this;
    //   this.spinning = true;
    //   this.$api
    //     .getDetailCustomer(this.currentCustInfo.id)
    //     .then(result => {
    //       result.data.items.forEach(item => {
    //         item.data.forEach(itemChild => {
    //           itemChild.selectable = "";
    //           // type 为 date 日期格式需要强制转化为 moment 格式
    //           if (itemChild.type == "timePicker") {
    //             itemChild.value =
    //               itemChild.value != "" ? moment(itemChild.value) : null;
    //           }
    //           // if(itemChild.type=="timePicker"){
    //           //   itemChild.value=itemChild.value?moment(itemChild.value):{}
    //           // }
    //         });
    //       });
    //       _this.customerInfoData = result.data;
    //       _this.spinning = false;
    //     })
    //     .catch(err => {
    //       let errMsg = err.message ? err.message : err;
    //       _this.$message.error("错误信息：" + errMsg, 5);
    //       _this.spinning = false;
    //     });
    // },
    addCustomerInfoToServer(data) {
      let _this = this;
      this.spinning = true;
      return new Promise((resolve, reject) => {
        _this.$api
          .addCustomerInfo(data)
          .then(result => {
            _this.spinning = false;
            const info = {
              id: result.data.id,
              owner: result.data.owner,
              mobile: result.data.mobile
            };
            _this.setCurrentCustInfo(info);
            let secondsToGo = 3;
            const key = 'updatable'
            _this.$notification['success']({
                  key,
                  message: '新增成功',
                  description: `名单新增成功，将于${secondsToGo}秒后跳转到详细客户资料页面展示该名单`
                })
            const interval = setInterval(() => {
              secondsToGo -= 1;
              _this.$notification['success']({
                key,
                message: '新增成功',
                description: `名单新增成功，将于${secondsToGo}秒后跳转到详细客户资料页面展示该名单`,
              });
            }, 1000);
            resolve(result)
            setTimeout(() => {
              clearInterval(interval)
              _this.$router.push("/customer/detail"); //根据勇哥意见，添加客户信息成功后跳转至详细资料界面
            }, secondsToGo * 1000);
          })
          .catch(err => {
            _this.spinning = false;
            _this.$myNotification.sendFailedNotification(err,6)
            reject(err)
          });
      })
    },
  }
};
</script>
<style>
.customerInfoHandle{
  text-align: center
}
</style>




